import React from "react";
import { Animated, Easing } from "react-native";

class AnimatedComponent extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      show: true,
      sliderShow: false,
    };
    this.opacityAnimate = new Animated.Value(1);
    this.sliderAnimate = new Animated.Value(0);
    this.opacity = {
      opacity: this.opacityAnimate,
    };
    this.slider = {
      transform: [
        {
          translateX: this.sliderAnimate.interpolate({
            inputRange: [0, 1],
            outputRange: [200, 0],
          }),
        },
      ],
    };
    this.show = this.show.bind(this);
    this.toggle = this.toggle.bind(this);
    this.stagger = this.stagger.bind(this);
    this.hidden = this.hidden.bind(this);
  }

  show() {
    this.setState({ show: true }, () => {
      Animated.timing(this.opacityAnimate, {
        toValue: 1,
        duration: 300,
        easing: Easing.linear,
        useNativeDriver: true,
      }).start();
    });
  }

  hidden() {
    if (this.state.show) {
      this.setState({ show: false });
    }
  }

  toggle() {
    this.animateToggle.apply(this, arguments);
  }

  stagger() {
    const args = arguments;
    if (!this.state.show) {
      this.setState({ show: true }, () => {
        this.animateStagger.apply(this, args);
      });
    } else {
      this.animateStagger.apply(this, args);
    }
  }

  animateStagger(delay = 2000) {
    Animated.stagger(delay, [
      Animated.timing(this.opacityAnimate, {
        toValue: 1,
        duration: 300,
        easing: Easing.linear,
        useNativeDriver: true,
      }),
      Animated.timing(this.opacityAnimate, {
        toValue: 0,
        duration: 300,
        easing: Easing.linear,
        useNativeDriver: true,
      }),
    ]).start();
  }

  animateToggle(delay = 2000) {
    if (!this.state.show) {
      this.setState({ show: true }, () => {
        Animated.timing(this.opacityAnimate, {
          toValue: 1,
          duration: 300,
          easing: Easing.linear,
          useNativeDriver: true,
        }).start();
      });
    } else {
      Animated.timing(this.opacityAnimate, {
        toValue: 0,
        duration: 300,
        easing: Easing.linear,
        useNativeDriver: true,
      }).start(() => {
        this.setState({ show: false });
      });
    }
  }

  sliderShow() {
    this.setState({ sliderShow: true }, () => {
      Animated.timing(this.sliderAnimate, {
        toValue: 1,
        duration: 100,
        easing: Easing.linear,
        useNativeDriver: true,
      }).start();
    });
  }
  sliderHidden() {
    this.setState({ sliderShow: false });
    this.sliderAnimate.setValue(0);
  }
}

export default AnimatedComponent;
