import Ionicons from "@expo/vector-icons/AntDesign";
import React from "react";
import {
  Animated,
  StyleSheet,
  Text,
  TextInput,
  TouchableOpacity,
  View,
} from "react-native";

import { Slider } from "@miblanchard/react-native-slider";
import NetworkInVideo from "../../businessComponents/NetworkInVideo";
import PlayListInVideo from "../../businessComponents/PlaylistInVideo";
import RateInVideo from "../../businessComponents/RateInVideo";
import ResizeModeInVideo from "../../businessComponents/ResizeModeInVideo";
import { isMobile, scaleSize } from "../utils";
import { MenusContext, VideoButtonContext } from "./context";
import AnimatedComponent from "./lib/AnimatedComponent";
import { formatTime } from "./lib/util";

/**
 * BottomMenus
 * @props renderBottomMenus
 * **/
export default class BottomMenus extends AnimatedComponent {
  render() {
    if (!this.state.show) return null;
    return (
      <VideoButtonContext.Consumer>
        {({ state: { episodes, currentKey, setCurrentKey } }: any) => (
          <MenusContext.Consumer>
            {({ state, props, styles }: any) => (
              <Animated.View style={[this.opacity]}>
                <View style={{ paddingHorizontal: scaleSize(10) }}>
                  <View
                    style={[
                      {
                        width: "100%",
                        backgroundColor: "transparent",
                        flexDirection: "row",
                        alignItems: "center",
                        justifyContent: "space-between",
                        height: scaleSize(48),
                      },
                      props.isFullScreen && {
                        height: scaleSize(40),
                        paddingHorizontal: scaleSize(6),
                      },
                    ]}
                  >
                    {!props.isFullScreen && (
                      <TouchableOpacity
                        style={{
                          paddingLeft: scaleSize(10),
                          paddingRight: scaleSize(20),
                        }}
                        onPress={props.setPaused}
                      >
                        <Ionicons
                          name={
                            Boolean(isMobile ? state.shouldPlay : state.isPlaying) ? "pause" : "caretright"
                          }
                          color="white"
                          size={scaleSize(22)}
                        />
                      </TouchableOpacity>
                    )}
                    <View
                      style={{
                        flex: 1,
                        flexDirection: "row",
                        alignItems: "center",
                        justifyContent: "space-between",
                      }}
                    >
                      <Text
                        style={[
                          {
                            color: "white",
                            alignSelf: "center",
                            width: scaleSize(62),
                            fontSize: scaleSize(12),
                            fontWeight: "bold",
                          },
                          props.isFullScreen && {
                            fontWeight: "normal",
                            fontSize: scaleSize(13),
                            marginRight: scaleSize(9),
                          },
                        ]}
                      >
                        {formatTime(
                          state.sliderAutoEnable
                            ? state.currentTime
                            : state.currentTime_copy,
                          true
                        )}
                      </Text>

                      <View
                        style={{
                          flex: 1,
                          position: "relative",
                          justifyContent: "center",
                        }}
                      >
                        {/********** playableDuration **********/}
                        <View style={{ width: "100%", position: "absolute" }}>
                          <Slider
                            //@ts-ignore
                            style={{ flex: 1 }}
                            containerStyle={{ flex: 1 }}
                            value={state.playableDurationMillis}
                            trackStyle={{ height: scaleSize(2) }}
                            maximumValue={Math.floor(state.durationMillis || 0)}
                            minimumValue={0}
                            minimumTrackTintColor={"#ffffff"}
                            maximumTrackTintColor={"rgba(248,248,248,0.5)"}
                            thumbTintColor={"transparent"}
                          />
                        </View>
                        {/********** currentTime **********/}
                        <Slider
                          //@ts-ignore
                          style={{ flex: 1 }}
                          containerStyle={{ flex: 1 }}
                          value={Math.floor(
                            state.sliderAutoEnable
                              ? state.currentTime
                              : state.currentTime_copy
                          )}
                          maximumValue={Math.floor(state.durationMillis || 0)}
                          minimumValue={0}
                          minimumTrackTintColor="#e45c9c"
                          maximumTrackTintColor="transparent"
                          thumbTintColor="#ffffff"
                          trackStyle={{ height: scaleSize(2) }}
                          thumbStyle={
                            props.isFullScreen
                              ? state.sliderAutoEnable
                                ? style.full
                                : style.full2
                              : state.sliderAutoEnable
                                ? style.tiny
                                : style.tiny2
                          }
                          onSlidingComplete={props.onSlidingComplete}
                          onValueChange={(val) => {
                            props.onCurrentTimeProgress(
                              Array.isArray(val) ? val[0] : val
                            );
                          }}
                        />
                      </View>
                      <Text
                        style={[
                          {
                            color: "white",
                            alignSelf: "center",
                            width: scaleSize(62),
                            fontSize: scaleSize(12),
                            fontWeight: "bold",
                            marginLeft: scaleSize(10),
                          },
                          props.isFullScreen && {
                            fontWeight: "normal",
                            fontSize: scaleSize(13),
                            marginLeft: scaleSize(19),
                          },
                        ]}
                      >
                        {formatTime(state.durationMillis, true)}
                      </Text>
                    </View>
                    {!props.isFullScreen && isMobile && (
                      <TouchableOpacity
                        style={{
                          paddingRight: scaleSize(10),
                          paddingLeft: scaleSize(15),
                          backgroundColor: "transparent",
                        }}
                        onPress={props.setFullScreen}
                      >
                        <Ionicons
                          name="scan1"
                          color="white"
                          size={scaleSize(22)}
                        />
                      </TouchableOpacity>
                    )}
                  </View>
                  {props.isFullScreen && (
                    <View
                      style={[styles.flex, { paddingBottom: scaleSize(30) }]}
                    >
                      <TouchableOpacity
                        onPress={props.setPaused}
                        style={{ paddingRight: scaleSize(15) }}
                      >
                        <Ionicons
                          name={Boolean(isMobile ? state.shouldPlay : state.isPlaying) ? "pause" : "caretright"}
                          color="white"
                          size={scaleSize(20)}
                        />
                      </TouchableOpacity>
                      <TouchableOpacity
                        onPress={() => {
                          const index = episodes.findIndex(
                            (item: any) =>
                              currentKey.playlist_key === item.playlist_key
                          );

                          if (index - 1 >= 0) {
                            props.setCurrentTime(() => {
                              setCurrentKey(episodes[index - 1]);
                            });
                          }
                        }}
                        style={{ paddingHorizontal: scaleSize(15) }}
                      >
                        <Ionicons
                          name={"stepforward"}
                          color="white"
                          size={scaleSize(20)}
                        />
                      </TouchableOpacity>
                      {false && (
                        <>
                          <TouchableOpacity
                            style={{
                              paddingLeft: scaleSize(10),
                              paddingRight: scaleSize(15),
                            }}
                          >
                            <Ionicons
                              name={"questioncircle"}
                              color="white"
                              size={scaleSize(20)}
                            />
                          </TouchableOpacity>
                          <TouchableOpacity
                            style={{
                              paddingLeft: scaleSize(15),
                              paddingRight: scaleSize(20),
                            }}
                          >
                            <Ionicons
                              name={"questioncircle"}
                              color="white"
                              size={scaleSize(20)}
                            />
                          </TouchableOpacity>
                          <View
                            onStartShouldSetResponder={() => true}
                            style={{
                              flex: 1,
                              height: scaleSize(30),
                              backgroundColor: "transparent",
                            }}
                          >
                            <TextInput
                              style={{
                                width: "100%",
                                height: "100%",
                                backgroundColor: "rgba(255,255,255,0.2)",
                                borderRadius: scaleSize(30),
                                paddingHorizontal: 15,
                                fontSize: scaleSize(14),
                              }}
                              placeholder="..."
                              placeholderTextColor="rgba(255,255,255,0.7)"
                            />
                          </View>
                        </>
                      )}
                      <View
                        style={{
                          width: scaleSize(5),
                          flex: 1,
                          justifyContent: "flex-end",
                        }}
                      />
                      <ResizeModeInVideo title="" />
                      <NetworkInVideo title="" />
                      <RateInVideo title="" />
                      <PlayListInVideo title="" />
                    </View>
                  )}
                </View>
              </Animated.View>
            )}
          </MenusContext.Consumer>
        )}
      </VideoButtonContext.Consumer>
    );
  }
}

const style = StyleSheet.create({
  tiny: {
    width: scaleSize(8),
    height: scaleSize(10),
  },
  tiny2: {
    width: scaleSize(10),
    height: scaleSize(14),
  },
  full: {
    width: scaleSize(10),
    height: scaleSize(14),
  },
  full2: {
    width: scaleSize(12),
    height: scaleSize(16),
  },
});
