import { Entypo } from "@expo/vector-icons";
import Ionicons from "@expo/vector-icons/AntDesign";
import React from "react";

import { Animated, Text, TouchableOpacity, View } from "react-native";

import ResizeModeInVideo from "../../businessComponents/ResizeModeInVideo";
import { styles as s } from "../styles";
import { scaleSize } from "../utils";
import { MenusContext } from "./context";
import AnimatedComponent from "./lib/AnimatedComponent";
import { styles as VideoPlayerStyles } from "./lib/style";

/**
 * TopMenus
 * @props renderTopMenus
 * **/
export default class TopMenus extends AnimatedComponent {
  render() {
    if (!this.state.show) return null;
    return (
      <MenusContext.Consumer>
        {({ state, props, styles }: any) => (
          <Animated.View style={[this.opacity, styles.topMenusContainer]}>
            <View
              style={{
                height: scaleSize(50),
                backgroundColor: "transparent",
                flexDirection: "row",
                alignItems: "center",
                justifyContent: "space-between",
              }}
            >
              <View style={VideoPlayerStyles.titleWrapper}>
                <TouchableOpacity
                  style={{
                    paddingLeft: scaleSize(15),
                    paddingVertical: scaleSize(10),
                    flexDirection: "row",
                    alignItems: "center",
                  }}
                  onPress={props.setNavigator}
                >
                  <Ionicons name="left" color="white" size={scaleSize(24)} />
                  {props.isFullScreen && (
                    <Text
                      style={[
                        {
                          color: "#fff",
                          fontSize: scaleSize(16),
                          paddingLeft: scaleSize(10),
                        },
                      ]}
                    >
                      {props.title}
                    </Text>
                  )}
                </TouchableOpacity>
              </View>
              {false&&<View style={[s.flex]}>
                {props.isFullScreen && (
                  <TouchableOpacity
                    style={{ paddingHorizontal: scaleSize(14) }}
                  >
                    <Ionicons
                      name="questioncircle"
                      color="white"
                      size={scaleSize(20)}
                    />
                  </TouchableOpacity>
                )}
                {props.isFullScreen && (
                  <ResizeModeInVideo />
                )}
                <TouchableOpacity style={{ paddingHorizontal: scaleSize(14) }}>
                  <Entypo name="tv" size={scaleSize(20)} color="white" />
                </TouchableOpacity>
                {props.isFullScreen && (
                  <TouchableOpacity
                    style={{
                      paddingLeft: scaleSize(14),
                      paddingRight: scaleSize(25),
                    }}
                  >
                    <Ionicons
                      name="export"
                      color="white"
                      size={scaleSize(20)}
                    />
                  </TouchableOpacity>
                )}
              </View>}
            </View>
          </Animated.View>
        )}
      </MenusContext.Consumer>
    );
  }
}
