import { FontAwesome5 } from "@expo/vector-icons";
import React from "react";
import {
  Dimensions,
  Modal,
  ScrollView,
  StyleSheet,
  Text,
  TouchableOpacity,
  View,
} from "react-native";
import { SafeAreaView } from "react-native-safe-area-context";
import ModalView from "../components/ModalView";
import {
  MenusContext,
  VideoButtonContext,
} from "../components/VideoPlayer/context";
import { scaleSize } from "../components/utils";

class PlayListInVideo extends React.Component<any, any> {
  state = {
    visible: false,
  };

  setVisible = (val: any) => {
    this.setState({ visible: val });
  };

  render() {
    const { visible } = this.state;
    return (
      <MenusContext.Consumer>
        {({ props: { setCurrentTime } }: any) => (
          <VideoButtonContext.Consumer>
            {({
              state: { a2z, episodes, setAToZ, currentKey, setCurrentKey },
            }: any) => (
              <>
                <Button
                  title=""
                  onPress={() => {
                    this.setVisible(true);
                  }}
                />
                <Modal
                  visible={visible}
                  transparent
                  onRequestClose={() => false}
                  animated={false}
                  supportedOrientations={["portrait", "landscape"]}
                >
                  <ModalView
                    style={{
                      justifyContent: "flex-end",
                      alignItems: "flex-end",
                    }}
                    animationType="slide-right"
                    visible={visible}
                    transparent
                    background={{ backgroundColor: "transparent" }}
                    onRequestClose={() => false}
                    onMaskPress={() => this.setVisible(false)}
                  >
                    <View
                      style={{
                        backgroundColor: "rgba(0,0,0,0.8)",
                        width: scaleSize(300),
                        height: Dimensions.get("window").height,
                        maxHeight: Dimensions.get("window").height,
                      }}
                    >
                      <SafeAreaView style={{ flex: 1 }}>
                        <View
                          style={{
                            flexDirection: "row",
                            justifyContent: "space-between",
                            alignItems: "center",
                            paddingTop: scaleSize(20),
                            paddingHorizontal: scaleSize(10),
                            marginBottom: scaleSize(15),
                          }}
                        >
                          <Text
                            style={{
                              color: "#e6e6e6",
                              fontSize: scaleSize(15),
                            }}
                          >
                            
                          </Text>
                          <TouchableOpacity
                            onPress={() => setAToZ(!a2z)}
                            style={{
                              padding: 4,
                              flexDirection: "row",
                              alignItems: "center",
                            }}
                          >
                            <FontAwesome5
                              name={a2z ? "sort-alpha-up" : "sort-alpha-up-alt"}
                              size={scaleSize(16)}
                              color="#fff"
                            />
                            <Text
                              style={{
                                color: "#fff",
                                fontSize: scaleSize(14),
                                paddingLeft: scaleSize(5),
                                paddingRight: scaleSize(20),
                              }}
                            >
                              {a2z ? "" : ""}
                            </Text>
                          </TouchableOpacity>
                        </View>
                        <ScrollView showsVerticalScrollIndicator={false}>
                          {(a2z ? [...episodes].reverse() : episodes).map(
                            (item, index) => (
                              <TouchableOpacity
                                key={index}
                                onPress={() => {
                                  setCurrentTime(() => {
                                    setCurrentKey(item);
                                  });
                                  this.setVisible(false);
                                }}
                                style={[
                                  style.button,
                                  currentKey.playlist_key === item.playlist_key
                                    ? { backgroundColor: "#1e1f20" }
                                    : {},
                                ]}
                              >
                                <View style={[style.buttonInner]}>
                                  <Text
                                    key={index}
                                    style={[
                                      currentKey.playlist_key ===
                                      item.playlist_key
                                        ? { color: "#f74e9e" }
                                        : { color: "#fff" },
                                    ]}
                                  >
                                    {item.name}
                                  </Text>
                                </View>
                              </TouchableOpacity>
                            )
                          )}
                        </ScrollView>
                      </SafeAreaView>
                    </View>
                  </ModalView>
                </Modal>
              </>
            )}
          </VideoButtonContext.Consumer>
        )}
      </MenusContext.Consumer>
    );
  }
}

export default PlayListInVideo;

const style = StyleSheet.create({
  button: {
    height: scaleSize(40),
    paddingHorizontal: scaleSize(5),
    borderRadius: scaleSize(5),
    marginLeft: scaleSize(15),
    marginRight: scaleSize(15),
    marginBottom: scaleSize(15),
  },
  buttonInner: {
    flex: 1,
    borderRadius: scaleSize(5),
    justifyContent: "center",
    alignItems: "center",
    textAlign: "center",
  },
});

function Button({ title, onPress }: any) {
  return (
    <TouchableOpacity
      style={{
        paddingHorizontal: scaleSize(12),
        paddingVertical: scaleSize(10),
      }}
      onPress={onPress}
    >
      <Text
        style={{ color: "#fff", fontWeight: "bold", fontSize: scaleSize(14) }}
      >
        {title}
      </Text>
    </TouchableOpacity>
  );
}
