import React from "react";
import {
  Dimensions,
  Modal,
  StyleSheet,
  Text,
  TouchableOpacity,
  View,
} from "react-native";
import { SafeAreaView } from "react-native-safe-area-context";
import ModalView from "../components/ModalView";
import {
  MenusContext,
  VideoButtonContext,
} from "../components/VideoPlayer/context";
import { scaleSize } from "../components/utils";

class RateInVideo extends React.Component<any, any> {
  state = {
    visible: false,
  };

  setVisible = (val: any) => {
    this.setState({ visible: val });
  };

  render() {
    const { visible } = this.state;
    const rateObj = {
      // 3: "3.0X",
      2: "2.0X",
      1.5: "1.5X",
      1.25: "1.25X",
      1: "",
      0.75: "0.75X",
    };
    const rateArray = [2, 1.5, 1.25, 1, 0.75];
    return (
      <MenusContext.Consumer>
        {({ state: { rate }, props: { setRate } }: any) => (
          <VideoButtonContext.Consumer>
            {({ state: { currentKey } }: any) => (
              <>
                <Button
                  title=""
                  onPress={() => {
                    this.setVisible(true);
                  }}
                />
                <Modal
                  visible={visible}
                  transparent
                  onRequestClose={() => false}
                  animated={false}
                  supportedOrientations={["portrait", "landscape"]}
                >
                  <ModalView
                    style={{
                      justifyContent: "flex-end",
                      alignItems: "flex-end",
                    }}
                    animationType="slide-right"
                    visible={visible}
                    transparent
                    background={{ backgroundColor: "transparent" }}
                    onRequestClose={() => false}
                    onMaskPress={() => this.setVisible(false)}
                  >
                    <View
                      style={{
                        backgroundColor: "rgba(0,0,0,0.8)",
                        width: scaleSize(280),
                        height: Dimensions.get("window").height,
                        maxHeight: Dimensions.get("window").height,
                      }}
                    >
                      <SafeAreaView
                        style={{
                          flex: 1,
                          justifyContent: "space-between",
                          paddingVertical: scaleSize(20),
                        }}
                      >
                        {rateArray.map((item: any, index: number) => (
                          <TouchableOpacity
                            key={index}
                            onPress={() => {
                              setRate(item);
                              this.setVisible(false);
                            }}
                            style={[
                              style.button,
                              +rate === +item
                                ? { backgroundColor: "#1e1f20" }
                                : {},
                            ]}
                          >
                            <View style={[style.buttonInner]}>
                              <Text
                                key={index}
                                style={[
                                  { fontSize: scaleSize(15) },
                                  +rate === +item
                                    ? { color: "#f74e9e" }
                                    : { color: "#fff" },
                                ]}
                              >
                                {rateObj[item]}
                              </Text>
                            </View>
                          </TouchableOpacity>
                        ))}
                      </SafeAreaView>
                    </View>
                  </ModalView>
                </Modal>
              </>
            )}
          </VideoButtonContext.Consumer>
        )}
      </MenusContext.Consumer>
    );
  }
}

export default RateInVideo;

const style = StyleSheet.create({
  button: {
    height: scaleSize(50),
    paddingHorizontal: scaleSize(5),
    borderRadius: scaleSize(5),
    marginLeft: scaleSize(15),
    marginRight: scaleSize(25),
  },
  buttonInner: {
    flex: 1,
    borderRadius: scaleSize(5),
    justifyContent: "center",
    alignItems: "center",
    textAlign: "center",
  },
});

function Button({ title, onPress }: any) {
  return (
    <TouchableOpacity
      style={{
        paddingHorizontal: scaleSize(12),
        paddingVertical: scaleSize(10),
      }}
      onPress={onPress}
    >
      <Text
        style={{ color: "#fff", fontWeight: "bold", fontSize: scaleSize(14) }}
      >
        {title}
      </Text>
    </TouchableOpacity>
  );
}
