import {
  default as AntDesign,
  default as Ionicons,
} from "@expo/vector-icons/AntDesign";
import FontAwesome from "@expo/vector-icons/FontAwesome";
import MaterialIcons from "@expo/vector-icons/MaterialIcons";
import Octicons from "@expo/vector-icons/Octicons";
import { Slider } from "@miblanchard/react-native-slider";
import AsyncStorage from "@react-native-async-storage/async-storage";
import { Audio, ResizeMode, Video } from "expo-av";
import * as Brightness from "expo-brightness";
import * as Clipboard from "expo-clipboard";
import * as Haptics from "expo-haptics";
import { router } from "expo-router";
import * as ScreenOrientation from "expo-screen-orientation";
import React from "react";
import {
  ActivityIndicator,
  Animated,
  Platform,
  Text,
  TouchableOpacity,
  View,
} from "react-native";
import * as Progress from "react-native-progress";
import { HISTORY_STORAGE } from "../../constants";
import { isMobile, scaleSize } from "../utils";
import BottomMenus from "./BottomMenus";
import TopMenus from "./TopMenus";
import { MenusContext } from "./context";
import AnimatedComponent from "./lib/AnimatedComponent";
import ResponderView from "./lib/ResponderView";
import { styles as VideoPlayerStyles } from "./lib/style";
import { deepMergeStyle, formatTime } from "./lib/util";

class VideoPlayer extends React.PureComponent {
  static defaultProps = {
    onLoad() {},
    onError() {},
    renderCenterMenus: null,
    renderTopMenus: null,
    renderBottomMenus: null,
    renderSeekTime: null,
    renderLoading: null,
    setFullScreen() {},
    setNavigator() {},
    setSetting() {},
    durationMillis: 0,
    currentTime: 0, // positionMillis
    isBuffering: true,
    isLoaded: false,
    isLooping: false,
    isMuted: false,
    isPlaying: false,
    rate: 1,
    volume: 1,
  };

  constructor(props) {
    super(props);
    this.state = {
      orientation: "portrait", //landscape

      pointerEvents: "none",
      width: props.width,

      //shouldComponentUpdate
      sliderAutoEnable: true,

      //video props
      durationMillis: props.durationMillis,
      currentTime: props.currentTime,
      currentTime_copy: 0,
      isBuffering: props.isBuffering,
      isLoaded: props.isLoaded,
      isLooping: props.isLooping,
      isMuted: props.isMuted,
      isPlaying: props.isPlaying,
      rate: props.rate,
      volume: props.volume,
      resizeMode: ResizeMode.CONTAIN,

      hideUserGuide: isMobile,
      brightness: 1,

      error: undefined,
    };
    this.longTime = new Date().getTime();
  }

  async componentDidMount() {
    const { status } = await Brightness.requestPermissionsAsync();
    if (status === "granted") {
      const brightness = await Brightness.getBrightnessAsync();
      this.brightness = brightness;
      this.setState({ brightness });
    }

    if (isMobile) {
      await Audio.setAudioModeAsync({ playsInSilentModeIOS: true });
      this._root?.loadAsync?.(this.props.source, { shouldPlay: true }, true);
    }
  }

  async componentWillUnmount() {
    this._root.pauseAsync();
    const storage = await AsyncStorage.getItem(HISTORY_STORAGE).catch(() => {});
    const newStorage = JSON.parse(storage || "[]");
    const oldIndex = newStorage.findIndex(
      (item) => item.defaultKey === this.props.defaultKey
    );
    if (oldIndex > -1) {
      newStorage.splice(oldIndex, 1);
    }
    newStorage.unshift({
      currentTime: this.state.currentTime,
      durationMillis: this.state.durationMillis,
      currentKey: this.props.currentKey,
      defaultKey: this.props.defaultKey,
      title: this.props.title,
      thumbnail: this.props.thumbnail,
    });

    await AsyncStorage.setItem(HISTORY_STORAGE, JSON.stringify(newStorage));

    if (isMobile) {
      ScreenOrientation.lockAsync(
        ScreenOrientation.OrientationLock.PORTRAIT_UP
      );
    }
  }

  reload = (uri) => {
    this.longTime = new Date().getTime();
    this.setState(
      {
        pointerEvents: "none",
        isBuffering: true,
        isLoaded: false,
        error: undefined,
      },
      () => {
        this._root.loadAsync(
          { uri },
          {
            shouldPlay: true,
            positionMillis: (this.state.currentTime || 0) * 1000,
          },
          false
        );
      }
    );
  };

  onReplay = () => {
    this.reload(this.props.source.uri);
  };

  /******************** responder event *******************/
  _handleSingleTouch = () => {
    this.toggleMenusComponent();
  };

  _handleDoubleTouch = () => {
    this.setPaused();
  };

  _handleLeftAndRightMove = async (offset, pageX) => {
    //show component when touch move
    // this.showSeekTimerComponent();
    this.showMenusComponent();

    //update currentTime
    const durationMillis = this.state.durationMillis;
    let currentTime_copy = this.state.currentTime_copy || 0;
    currentTime_copy += offset;
    if (currentTime_copy < 0) {
      currentTime_copy = 0;
    } else if (currentTime_copy > durationMillis) {
      currentTime_copy = durationMillis;
    } else {
      currentTime_copy = currentTime_copy;
    }
    this.onCurrentTimeProgress(currentTime_copy);
  };

  _handleLeftAndRightMoveComplete = async () => {
    await this.onSlidingComplete(this.state.currentTime_copy);
  };

  _handleUpAndDownMoveInLeft = (offset) => {
    let brightness = this.brightness;
    brightness += offset / 100;
    if (brightness <= 0 || isNaN(brightness)) {
      this.brightness = 0;
    } else if (brightness > 1) {
      this.brightness = 1;
    } else {
      this.brightness = brightness;
    }
    this.setBrightness(this.brightness);
    this.refs._volumeMenus.hidden();
    this.refs._brightnessMenus.stagger();
  };

  _handleUpAndDownMoveInRight = async (offset) => {
    let innerVolume = this.state.innerVolume;
    innerVolume += offset / 100;

    this.setState(
      {
        innerVolume:
          innerVolume <= 0 || isNaN(innerVolume)
            ? 0
            : innerVolume > 1
              ? 1
              : innerVolume,
      },
      () => {
        this._root.setStatusAsync({
          volume: this.state.innerVolume.toFixed(1),
        });
      }
    );
    this.refs._brightnessMenus.hidden();
    this.refs._volumeMenus.stagger();
  };

  // 
  _handleLongTouchDown = async () => {
    if (this.state.isPlaying) {
      isMobile && Haptics.impactAsync(Haptics.ImpactFeedbackStyle.Heavy);
      await this._root.setStatusAsync({ rate: 2 });
      this.refs._rateMenus?.show();
    }
  };

  // 
  _handleLongTouchUp = () => {
    this.refs._rateMenus.hidden();
    this._root.setStatusAsync({ rate: 1 });
  };

  /******************** custom event *******************/
  showSeekTimerComponent = () => {
    this.refs._seekTime.show();
  };
  showMenusComponent = () => {
    this.refs._lockMenus.show();
    this.refs._topMenus.show();
    this.refs._bottomMenus.show();
  };
  toggleMenusComponent = () => {
    this.refs._lockMenus?.toggle();
    this.refs._topMenus?.toggle();
    this.refs._bottomMenus?.toggle();
    this.refs._settingMenus?.sliderHidden();
  };

  render() {
    const { pointerEvents, width, resizeMode } = this.state;
    const { children, controls, height } = this.props;
    const boxStyle = { width, height };
    return (
      <MenusContext.Provider
        value={{
          state: this.state,
          props: {
            ...this.props,
            onCurrentTimeProgress: this.onCurrentTimeProgress,
            onSlidingComplete: this.onSlidingComplete,
            setPaused: this.setPaused,
            setFullScreen: this.setFullScreen,
            setNavigator: this.setNavigator,
            setLock: this.setLock,
            setVolumne: this.setVolumne,
            setRate: this.setRate,
            setResizeMode: this.setResizeMode,
            setBrightness: this.setBrightness,
            setSetting: this.setSetting,
            setCurrentTime: this.setCurrentTime,
            onReplay: this.onReplay,
          },
          styles: deepMergeStyle(VideoPlayerStyles, this.props.styles),
        }}
      >
        <View style={[VideoPlayerStyles.container, this.props.containerStyle]}>
          <Video
            resizeMode={resizeMode}
            style={[VideoPlayerStyles.video, boxStyle]}
            videoStyle={[VideoPlayerStyles.video, boxStyle]}
            children={undefined}
            ref={(c) => {
              this._root = c;
            }}
            {...(isMobile ? {} : { source: this.props.source })}
            onPlaybackStatusUpdate={this.onPlaybackStatusUpdate}
            isLooping={this.state.isLooping}
            onLoad={this.onLoad}
            onLoadStart={this.onLoadStart}
            onError={this.onError}
            onFullscreenUpdate={this.onFullscreenUpdate}
          />
          {/* User Guide */}
          {!this.state.hideUserGuide && (
            <UserGuide
              onTap={() => {
                this.setState({ hideUserGuide: true });
                this._root?.playAsync();
              }}
            />
          )}
          {/* TODO-luokun: isLoader  true bufferingResponder */}
          {Boolean(this.state.error) ? (
            <ErrorComponent />
          ) : this.state.lock ? (
            <View
              style={[VideoPlayerStyles.centerMenusContainer]}
              onStartShouldSetResponder={() => true}
              onResponderRelease={() => {
                this.refs._lockMenus.toggle();
              }}
            >
              <View
                style={{
                  flex: 1,
                  justifyContent: "center",
                  alignItems: "center",
                  width: "100%",
                }}
              >
                <Lock ref="_lockMenus" />
              </View>
            </View>
          ) : (
            !controls && (
              <ResponderView
                style={VideoPlayerStyles.touchView}
                pointerEvents={pointerEvents}
                handleSingleTouch={this._handleSingleTouch}
                handleDoubleTouch={this._handleDoubleTouch}
                handleLeftAndRightMove={this._handleLeftAndRightMove}
                handleLeftAndRightMoveComplete={
                  this._handleLeftAndRightMoveComplete
                }
                handleUpAndDownMoveInLeft={this._handleUpAndDownMoveInLeft}
                handleUpAndDownMoveInRight={this._handleUpAndDownMoveInRight}
                handleLongTouchUp={this._handleLongTouchUp}
                handleLongTouchDown={this._handleLongTouchDown}
              >
                {/********** loading **********/}

                {Boolean(this.state.isBuffering) && <Loading />}

                {/* TODO-luokun: loading */}
                {pointerEvents === "auto" && (
                  <>
                    {/********** rate menus **********/}
                    <RateMenus ref="_rateMenus" />

                    {/********** durationMillis menus **********/}
                    <DurationMillisMenus ref="_durrationMillisMenus" />

                    {/********** volume menus **********/}
                    <Volume ref="_volumeMenus" />

                    {/********** light menus **********/}
                    <BrightnessMenus ref="_brightnessMenus" />

                    {/********** top menus **********/}

                    <TopMenus
                      //   isPlaying={this.state.isPlaying}
                      ref="_topMenus"
                    />

                    {/********** lock menus **********/}

                    <Lock ref="_lockMenus" />
                    <SettingMenus
                      ref="_settingMenus"
                      // volume={this.state.volumne}
                      // setVolumne={this.setVolumne}
                    />

                    {/********** bottom menus **********/}
                    <View style={VideoPlayerStyles.bottomMenusContainer}>
                      {/* <SeekTime ref="_seekTime" /> */}
                      <BottomMenus
                        //   isPlaying={this.state.isPlaying}
                        ref="_bottomMenus"
                      />
                      {/* <Progress.Bar
                    unfilledColor="#FFFFFF"
                    color="pink"
                    progress={
                      this.state.durationMillis
                        ? this.state.currentTime / this.state.durationMillis
                        : 0
                    }
                    width={null}
                    height={2}
                  /> */}
                    </View>

                    {/********** custom view **********/}
                    {!!children && (
                      <MenusContext.Consumer>{children}</MenusContext.Consumer>
                    )}
                  </>
                )}
              </ResponderView>
            )
          )}
        </View>
      </MenusContext.Provider>
    );
  }

  /******************** video event *******************/
  onLoad = (status) => {
    console.log(
      this.props.source.uri,
      " " +
        Number(new Date().getTime() - this.longTime) +
        "ms"
    );
    if (isMobile) {
      this.setState({
        pointerEvents: "auto",
        innerVolume: status.volume,
        durationMillis: status.durationMillis,
        currentTime: status.positionMillis,
        currentTime_copy: status.positionMillis,
      });
    } else {
      this.setState({
        pointerEvents: "auto",
      });
    }
  };

  onError = (e) => {
    console.log(
      this.props.source.uri,
      " " +
        Number(new Date().getTime() - this.longTime) +
        "ms",
      e
    );
    this.setState({ error: e || "" });
    if (this.props.onError) {
      this.props.onError(e);
    }
  };

  setPaused = () => {
    const isPlaying = this.state.isPlaying;
    if (!isPlaying) {
      this._root.playAsync();
    } else {
      this._root.pauseAsync();
    }
  };

  setFullScreen = async () => {
    if (this.props.orientation === "") {
      ScreenOrientation.lockAsync(
        ScreenOrientation.OrientationLock.PORTRAIT_UP
      );
      this.props.setOrientation("");
    } else {
      ScreenOrientation.lockAsync(
        ScreenOrientation.OrientationLock.LANDSCAPE_RIGHT
      );
      this.props.setOrientation("");
    }
  };

  setNavigator = () => {
    if (this.props.isFullScreen) {
      this.setFullScreen();
    } else {
      router.back();
    }
  };

  setLock = () => {
    const { lock } = this.state;
    this.setState({ lock: !lock });
  };

  setVolumne = (innerVolume) => {
    if (isNaN(innerVolume)) return;
    this.setState({ innerVolume });
    this._root.setStatusAsync({ volume: innerVolume });
  };

  setBrightness = (brightness) => {
    if (isNaN(brightness)) return;
    this.setState({ brightness });
    if (["android", "ios"].includes(Platform.OS)) {
      Brightness.setBrightnessAsync(brightness);
    }
  };

  setRate = async (rate) => {
    await this._root.setStatusAsync({ rate });
  };

  setResizeMode = (val) => {
    this.setState({ resizeMode: val });
  };

  setSetting = () => {
    this.refs._lockMenus.toggle();
    this.refs._topMenus.toggle();
    this.refs._bottomMenus.toggle();
    this.refs._settingMenus.sliderShow();
  };

  setCurrentTime = (cb) => {
    this.setState({ currentTime: 0 }, cb);
  };

  onPlaybackStatusUpdate = (status) => {
    // console.log({
    //   isPlaying: status.isPlaying,
    //   shouldPlay: status.shouldPlay,
    // }, "onPlaybackStatusUpdate");
    this.setState({
      durationMillis: status.durationMillis / 1000,
      currentTime: status.positionMillis / 1000,
      isBuffering: status.isBuffering,
      isLoaded: status.isLoaded,
      isLooping: status.isLooping,
      isMuted: status.isMuted,
      isPlaying: status.isPlaying,
      // pitchCorrectionQuality: status.pitchCorrectionQuality,
      playableDurationMillis: status.playableDurationMillis / 1000, // 
      // didJustFinish: status.didJustFinish,
      shouldPlay: status.shouldPlay, // shouldPlay,ref.method
      // volume: status.volume, // 
      rate: status.rate, // 
    });
  };

  onCurrentTimeProgress = (value) => {
    this.showMenusComponent();
    this.setState(
      {
        sliderAutoEnable: false,
        currentTime_copy: value,
      },
      () => {
        this.refs._durrationMillisMenus?.show?.();
      }
    );
  };

  onSlidingComplete = async (value) => {
    await this._root.setStatusAsync({ positionMillis: value * 1000 });
    this.setState({ sliderAutoEnable: true }, () => {
      this.refs._durrationMillisMenus?.hidden?.();
    });
  };
}

class SettingMenus extends AnimatedComponent {
  render() {
    if (!this.state.sliderShow) return null;
    return (
      <MenusContext.Consumer>
        {({ state, props, styles }) => (
          <>
            <Animated.View
              style={[styles.settingMenusContainer, this.slider]}
              onStartShouldSetResponder={() => true}
            />
            <Animated.View
              style={[
                styles.settingMenusContainer,
                {
                  backgroundColor: "transparent",
                  opacity: this.sliderAnimate,
                },
              ]}
              onStartShouldSetResponder={() => true}
            >
              <View style={styles.col}>
                <Text style={{ color: "white", marginBottom: 10 }}>
                  
                </Text>
                <View style={styles.flex}>
                  <TouchableOpacity style={{ paddingRight: 10 }}>
                    <Text style={{ color: "white" }}></Text>
                  </TouchableOpacity>
                  <TouchableOpacity style={{ paddingHorizontal: 10 }}>
                    <Text style={{ color: "white" }}></Text>
                  </TouchableOpacity>
                  <TouchableOpacity style={{ paddingHorizontal: 10 }}>
                    <Text style={{ color: "white" }}></Text>
                  </TouchableOpacity>
                </View>
              </View>
              <View style={styles.col}>
                <Text style={{ color: "white", marginBottom: 5 }}>
                  
                </Text>
                <View style={styles.flex}>
                  <Octicons
                    name="mute"
                    color="white"
                    size={20}
                    style={{ marginRight: 10 }}
                  />
                  <Slider
                    containerStyle={{ flex: 1 }}
                    style={{ flex: 1 }}
                    value={state.innerVolume}
                    onValueChange={(val) => {
                      props.setVolumne(Array.isArray(val) ? val[0] : val);
                    }}
                    minimumTrackTintColor="white"
                    maximumTrackTintColor="#cccccc"
                    thumbTintColor="white"
                    thumbStyle={{ width: 10, height: 10 }}
                  />
                  <Octicons
                    name="unmute"
                    color="white"
                    size={20}
                    style={{ marginLeft: 10 }}
                  />
                </View>
              </View>
              <View style={styles.col}>
                <Text style={{ color: "white", marginBottom: 5 }}>
                  
                </Text>
                <View style={styles.flex}>
                  <MaterialIcons
                    name="brightness-low"
                    color="white"
                    size={20}
                    style={{ marginRight: 10 }}
                  />
                  <Slider
                    containerStyle={{ flex: 1 }}
                    style={{ flex: 1 }}
                    value={state.brightness}
                    onValueChange={(val) => {
                      props.setBrightness(Array.isArray(val) ? val[0] : val);
                    }}
                    minimumTrackTintColor="white"
                    maximumTrackTintColor="#cccccc"
                    thumbTintColor="white"
                    thumbStyle={{ width: 10, height: 10 }}
                  />
                  <MaterialIcons
                    name="brightness-high"
                    color="white"
                    size={20}
                    style={{ marginLeft: 10 }}
                  />
                </View>
              </View>
            </Animated.View>
          </>
        )}
      </MenusContext.Consumer>
    );
  }
}

class Lock extends AnimatedComponent {
  render() {
    if (!this.state.show) return null;
    return (
      <MenusContext.Consumer>
        {({ state, props, styles }) => (
          <Animated.View
            style={[
              this.opacity,
              styles.lockContainer,
              {
                marginLeft: props.isFullScreen
                  ? props.insets.top
                  : scaleSize(15),
              },
            ]}
          >
            <View style={styles.mark} />
            <TouchableOpacity onPress={props.setLock}>
              <Ionicons
                name={state.lock ? "lock1" : "unlock"}
                color="white"
                size={24}
              />
            </TouchableOpacity>
          </Animated.View>
        )}
      </MenusContext.Consumer>
    );
  }
}

class Volume extends AnimatedComponent {
  constructor(props) {
    super(props);
    this.opacityAnimate = new Animated.Value(0);
  }

  render() {
    if (!this.state.show) return null;
    return (
      <MenusContext.Consumer>
        {({ state, props, styles }) => (
          <Animated.View
            style={[
              { opacity: this.opacityAnimate },
              styles.centerMenusContainer,
            ]}
          >
            <View style={[styles.modal]}>
              <Octicons
                name={state.innerVolume ? "unmute" : "mute"}
                color="white"
                size={20}
                style={{ marginHorizontal: 10 }}
              />
              <View style={styles.progress}>
                <Progress.Bar
                  unfilledColor="#FFFFFF"
                  color="pink"
                  width={null}
                  height={2}
                  progress={state.innerVolume} // video
                />
              </View>
            </View>
          </Animated.View>
        )}
      </MenusContext.Consumer>
    );
  }
}

