import NetInfo from "@react-native-community/netinfo";
import * as ScreenOrientation from "expo-screen-orientation";
import { StatusBar } from "expo-status-bar";
import * as React from "react";
import { ActivityIndicator, Dimensions, StyleSheet } from "react-native";
import { GestureHandlerRootView } from "react-native-gesture-handler";
// import Card from "../../components/Card";
import { View } from "./components/Themed";
import { VideoButtonContext } from "./components/VideoPlayer/context";
import VideoPlayer from "./components/VideoPlayer/expo-av";
import { isMobile, scaleSize, screenW } from "./components/utils";

export default function () {
  const video = React.useRef(null);
  const unsubscribe = React.useRef(() => {});

  const [contentLoading, setContentLoading] = React.useState(false);
  const [netinfo, setNetinfo] = React.useState({ isConnected: true } as any);

  React.useEffect(() => {
    if (isMobile) {
      ScreenOrientation.lockAsync(
        ScreenOrientation.OrientationLock.PORTRAIT_UP
      );
    }
    NetInfo.fetch().then((state) => {
      setNetinfo(state);
    });
    unsubscribe.current = NetInfo.addEventListener((state) => {
      setNetinfo(state);
    });
    return () => {
      if (unsubscribe.current) {
        unsubscribe.current();
      }
    };
  }, []);

  const [orientation, setOrientation] = React.useState(""); // 
  const isFullScreen =
    orientation === "";
  const height = scaleSize(212);

  const [a2z, setAToZ] = React.useState(true);
  const [episodes, setEpisodes] = React.useState([]);

  const insets = React.useRef();
  const inset2 = {top:0}
  React.useEffect(() => {
    insets.current = inset2;
    if (isMobile) {
      // @ts-ignore
      video.current?.reload?.(
        "https://media.w3.org/2010/05/sintel/trailer.mp4"
      );
    }
  }, []);

  console.log(screenW, "screenW", height);

  return (
    <VideoButtonContext.Provider
      value={{
        state: {
          uri: "",
          setUri: () => {},
          networlList: [],
          detail: {},
          a2z,
          setAToZ,
          episodes,
          setEpisodes,
          currentKey: "",
          setCurrentKey: () => {},
        },
      }}
    >
        <View
          style={[
            isFullScreen
              ? { paddingHorizontal: insets.current?.left || 0 }
              : { paddingTop: insets.current?.top || 0 },
            Boolean(isFullScreen && !isMobile)
              ? {
                  transform: [
                    { rotate: "90deg" },
                    {
                      translateY:
                        (Dimensions.get("window").height -
                          Dimensions.get("window").width) /
                        2,
                    },
                    {
                      translateX:
                        (Dimensions.get("window").height -
                          Dimensions.get("window").width) /
                        2,
                    },
                  ],
                  height: Dimensions.get("window").width,
                  width: Dimensions.get("window").height,
                  backgroundColor: "yellow",
                  overflow: "hidden",
                }
              : { flex: 1, backgroundColor: "#000" },
            !isMobile && {
              "-webkit-user-select": "none",
              "-moz-user-select": "none",
              "-o-user-select": "none",
              "user-select": "none",
            },
          ]}
        >
          <StatusBar style="light" hidden={false} />
          {/* TODO-luokun: videoscrollview */}
          <GestureHandlerRootView style={{ flex: 1 }}>
            {/* <ScrollView scrollEnabled={!isFullScreen} > */}
            {/****** video player components ******/}
            {Boolean(contentLoading) ? (
              <ActivityIndicator
                size={scaleSize(50)}
                color={"#fff"}
                style={isFullScreen ? { height: "100%" } : { height }}
              />
            ) : (
              <VideoPlayer
                ref={video}
                title=""
                styles={{ container: [{ backgroundColor: "gray" }] }}
                height={
                  isFullScreen
                    ? isMobile
                      ? screenW
                      : Dimensions.get("window").width
                    : height
                }
                width={
                  isFullScreen && !isMobile
                    ? Dimensions.get("window").height
                    : "100%"
                }
                source={{
                  uri: "https://media.w3.org/2010/05/sintel/trailer.mp4",
                }}
                orientation={orientation}
                setOrientation={setOrientation}
                isFullScreen={isFullScreen}
                insets={insets.current || { top: scaleSize(15) }}
                onError={() => {
                  NetInfo.fetch().then((state) => {
                    setNetinfo(state);
                  });
                }}
              />
            )}

            {!isFullScreen && (
              <View style={{ flex: 1, backgroundColor: "#FFFFFF" }}>
                {/****** user ******/}
                <View
                  style={{
                    padding: 20,
                    flexDirection: "row",
                    justifyContent: "space-between",
                  }}
                >
                  <View style={{ flexDirection: "row" }}>
                    <View
                      style={{
                        backgroundColor: "#ccc",
                        width: 50,
                        height: 50,
                        borderRadius: 25,
                      }}
                    />
                    <View
                      style={{
                        justifyContent: "space-between",
                        marginLeft: 10,
                      }}
                    >
                      <View
                        style={{
                          backgroundColor: "#ccc",
                          width: 80,
                          height: 20,
                        }}
                      />
                      <View
                        style={{
                          backgroundColor: "#ccc",
                          width: 100,
                          height: 20,
                        }}
                      />
                    </View>
                  </View>
                  <View
                    style={{
                      backgroundColor: "#ccc",
                      width: 80,
                      height: 35,
                      alignSelf: "center",
                    }}
                  />
                </View>

                {/****** video description ******/}
                <View
                  style={{
                    flex: 1,
                    justifyContent: "space-around",
                    padding: 20,
                  }}
                >
                  <View
                    style={{
                      backgroundColor: "#ccc",
                      flex: 1,
                      marginBottom: "2%",
                    }}
                  />
                  <View
                    style={{
                      backgroundColor: "#ccc",
                      flex: 1,
                      width: "80%",
                      marginBottom: "2%",
                    }}
                  />
                  <View style={{ flex: 1, flexDirection: "row", width: "60%" }}>
                    {Array.apply(null, Array(4)).map((a, i) => {
                      return (
                        <View
                          key={i}
                          style={{
                            backgroundColor: "#ccc",
                            flex: 1,
                            marginRight: 10,
                          }}
                        />
                      );
                    })}
                  </View>
                </View>

                {/****** recommended list ******/}
                <View
                  style={{
                    flex: 4,
                    justifyContent: "space-around",
                    padding: 10,
                  }}
                >
                  {Array.apply(null, Array(3)).map((a, i) => {
                    return (
                      <View
                        key={i}
                        style={{
                          height: "30%",
                          flexDirection: "row",
                          justifyContent: "space-around",
                        }}
                      >
                        <View
                          style={{
                            backgroundColor: "#ccc",
                            width: "40%",
                            height: "100%",
                          }}
                        />
                        <View
                          style={{
                            width: "55%",
                            height: "100%",
                            justifyContent: "space-between",
                          }}
                        >
                          <View
                            style={{
                              backgroundColor: "#ccc",
                              width: "80%",
                              height: "20%",
                            }}
                          />
                          <View
                            style={{
                              backgroundColor: "#ccc",
                              width: "30%",
                              height: "20%",
                            }}
                          />
                          <View
                            style={{
                              backgroundColor: "#ccc",
                              width: "40%",
                              height: "20%",
                            }}
                          />
                        </View>
                      </View>
                    );
                  })}
                </View>
              </View>
            )}
            {/* </ScrollView> */}
          </GestureHandlerRootView>
        </View>
    </VideoButtonContext.Provider>
  );
}

const style = StyleSheet.create({});
