import React from "react";
import { StyleSheet, View } from "react-native";

let touchTimer;
let touchLongTimer;
class ResponderView extends React.PureComponent {
  static defaultProps = {
    handleSingleTouch() {},
    handleDoubleTouch() {},
    handleLongTouchDown() {},
    handleLongTouchUp() {},
    handleLeftAndRightMove() {},
    handleLeftAndRightMoveComplete() {},
    handleUpAndDownMoveInLeft() {},
    handleUpAndDownMoveInRight() {},
    handleUpAndDownMoveInRightComplete() {},
    handleUpAndDownMoveInLeftComplete() {},
  };
  constructor(props) {
    super(props);
    this.state = {
      firstCoordinate: { x: 0, y: 0 }, // for judging LeftAndRightMove
      lastCoordinate: { x: 0, y: 0 }, // for calculating move offset
      moveEvent: null, // Responder Event
    };
    this.isLongTouching = false;
  }

  /**********           Handle Event           **********/

  handleSingleTouch = (e) => {
    this.props.handleSingleTouch(e);
  };

  handleDoubleTouch = (e) => {
    this.props.handleDoubleTouch(e);
  };

  /**
   * moveEventType: ['UpAndDownMoveInRight','UpAndDownMoveInLeft','LeftAndRightMove']
   *
   * @param offset:   Number( currentCoordinate - lastCoordinate )
   *
   * **/
  handleUpAndDownMoveInRight = (offset) => {
    this.props.handleUpAndDownMoveInRight(offset);
  };

  handleUpAndDownMoveInLeft = (offset) => {
    this.props.handleUpAndDownMoveInLeft(offset);
  };

  handleLeftAndRightMove = (offset, pageX) => {
    this.props.handleLeftAndRightMove(offset, pageX);
  };

  /**********            Render           **********/

  render() {
    return (
      <View
        ref="touchView"
        {...this.props}
        style={[styles.touchView, this.props.style]}
        onStartShouldSetResponder={this._onStartShouldSetResponder}
        onResponderGrant={this._onResponderGrant}
        onResponderMove={this._onResponderMove}
        onResponderRelease={this._onResponderRelease}
      >
        {this.props.children}
      </View>
    );
  }

  /**********           Life Cycle           **********/

  componentWillUnmount() {
    touchTimer && clearTimeout(touchTimer);
  }

  /**********    Gesture Responder System    **********/

  _onStartShouldSetResponder = (e) => {
    touchLongTimer = setTimeout(() => {
      this.props.handleLongTouchDown(e);
      this.isLongTouching = true;
      touchLongTimer = undefined;
    }, 1000);

    return true;
  };

  _onResponderGrant = async (e) => {
    const { pageX, pageY, locationX, locationY } = e.nativeEvent;
    this.refs.touchView.measure(async (ox, oy, width, height, px, py) => {
      await this.setState({
        firstCoordinate: { x: locationX, y: locationY },
        lastCoordinate: { x: pageX, y: pageY },
      });
    });
  };

  _onResponderMove = async (e) => {
    if (this.isLongTouching) return;

    e.persist();
    const { pageX, pageY, locationX, locationY } = e.nativeEvent;
    const { moveEvent, firstCoordinate, lastCoordinate } = this.state;

    // tiny move ignore
    if (
      !moveEvent &&
      Math.abs(pageX - lastCoordinate.x) < 5 &&
      Math.abs(pageY - lastCoordinate.y) < 5
    ) {
      return;
    }

    if (touchLongTimer) {
      clearTimeout(touchLongTimer);
      touchLongTimer = undefined;
    }

    this.refs.touchView.measure(async (ox, oy, width, height, px, py) => {
      if (moveEvent) {
        if (moveEvent === "LeftAndRightMove") {
          this.handleLeftAndRightMove(pageX - lastCoordinate.x, pageX);
        } else if (moveEvent === "UpAndDownMoveInRight") {
          this.handleUpAndDownMoveInRight(lastCoordinate.y - pageY);
        } else if (moveEvent === "UpAndDownMoveInLeft") {
          this.handleUpAndDownMoveInLeft(lastCoordinate.y - pageY);
        }
      } else {
        if (
          Math.abs(firstCoordinate.x - locationX) >
          Math.abs(firstCoordinate.y - locationY)
        ) {
          this.handleLeftAndRightMove(pageX - lastCoordinate.x, pageX);
          await this.setState({
            moveEvent: "LeftAndRightMove",
          });
        } else if (locationX > width / 2) {
          this.handleUpAndDownMoveInRight(lastCoordinate.y - pageY);
          await this.setState({
            moveEvent: "UpAndDownMoveInRight",
          });
        } else if (locationX < width / 2) {
          this.handleUpAndDownMoveInLeft(lastCoordinate.y - pageY);
          await this.setState({
            moveEvent: "UpAndDownMoveInLeft",
          });
        }
      }
    });

    this.setState({ lastCoordinate: { x: pageX, y: pageY } });
  };

  _onResponderRelease = async (e) => {
    const moveEvent = this.state.moveEvent;
    await this.setState({
      firstCoordinate: { x: 0, y: 0 },
      lastCoordinate: { x: 0, y: 0 },
      moveEvent: null,
    });
    if (moveEvent) {
      if (moveEvent == "LeftAndRightMove") {
        this.props.handleLeftAndRightMoveComplete();
      } else if (moveEvent === "UpAndDownMoveInRight") {
        this.props.handleUpAndDownMoveInRightComplete();
      } else if (moveEvent === "UpAndDownMoveInLeft") {
        this.props.handleUpAndDownMoveInLeftComplete();
      }
    } else {
      if (!touchTimer) {
        touchTimer = setTimeout(() => {
          this.handleSingleTouch(e);
          touchTimer = undefined;
        }, 300);
      } else {
        this.handleDoubleTouch(e);
        clearTimeout(touchTimer);
        touchTimer = undefined;
      }
    }

    if (this.isLongTouching) {
      this.isLongTouching = false;
      this.props.handleLongTouchUp(e);
    }

    if (touchLongTimer) {
      clearTimeout(touchLongTimer);
      touchLongTimer = undefined;
    }
  };
}

const styles = StyleSheet.create({
  touchView: {
    flex: 1,
    backgroundColor: "transparent",
    opacity: 1,
  },
});

export default ResponderView;
