import Ionicons from "@expo/vector-icons/AntDesign";
import { ResizeMode } from "expo-av";
import ModalView from "../components/ModalView";
import { MenusContext } from "../components/VideoPlayer/context";
import { scaleSize } from "../components/utils";

import React from "react";
import {
  Dimensions,
  Modal,
  StyleSheet,
  Text,
  TouchableOpacity,
  View,
} from "react-native";
import { SafeAreaView } from "react-native-safe-area-context";

class ResizeModeInVideo extends React.Component<any, any> {
  state = {
    visible: false,
  };

  setVisible = (val: any) => {
    this.setState({ visible: val });
  };

  render() {
    const { visible } = this.state;
    const resizeModeObj = {
      [ResizeMode.COVER]: "",
      [ResizeMode.CONTAIN]: "",
      [ResizeMode.STRETCH]: "",
    };
    const resizeModeArray = [
      ResizeMode.COVER,
      ResizeMode.CONTAIN,
      ResizeMode.STRETCH,
    ];
    return (
      <MenusContext.Consumer>
        {({ state: { resizeMode }, props: { setResizeMode } }: any) => (
          <>
            {false && (
              <TouchableOpacity
                style={{ paddingHorizontal: scaleSize(14) }}
                onPress={() => this.setVisible(true)}
              >
                <Ionicons name="scan1" color="white" size={scaleSize(20)} />
              </TouchableOpacity>
            )}
            <Button title={""} onPress={() => this.setVisible(true)} />
            <Modal
              visible={visible}
              transparent
              onRequestClose={() => false}
              animated={false}
              supportedOrientations={['portrait', 'landscape']}
            >
              <ModalView
                style={{
                  justifyContent: "flex-end",
                  alignItems: "flex-end",
                }}
                animationType="slide-right"
                visible={visible}
                transparent
                background={{ backgroundColor: "transparent" }}
                onRequestClose={() => false}
                onMaskPress={() => this.setVisible(false)}
              >
                <View
                  style={{
                    backgroundColor: "rgba(0,0,0,0.8)",
                    width: scaleSize(280),
                    height: Dimensions.get("window").height,
                    maxHeight: Dimensions.get("window").height,
                  }}
                >
                  <SafeAreaView
                    style={{
                      flex: 1,
                      justifyContent: "center",
                      paddingVertical: scaleSize(20),
                    }}
                  >
                    {resizeModeArray.map((item: any, index: number) => (
                      <TouchableOpacity
                        key={index}
                        onPress={() => {
                          setResizeMode(item);
                          this.setVisible(false);
                        }}
                        style={[
                          style.button,
                          resizeMode === item
                            ? { backgroundColor: "#1e1f20" }
                            : {},
                        ]}
                      >
                        <View style={[style.buttonInner]}>
                          <Text
                            key={index}
                            style={[
                              { fontSize: scaleSize(15) },
                              resizeMode === item
                                ? { color: "#f74e9e" }
                                : { color: "#fff" },
                            ]}
                          >
                            {resizeModeObj[item]}
                          </Text>
                        </View>
                      </TouchableOpacity>
                    ))}
                  </SafeAreaView>
                </View>
              </ModalView>
            </Modal>
          </>
        )}
      </MenusContext.Consumer>
    );
  }
}

export default ResizeModeInVideo;

const style = StyleSheet.create({
  button: {
    height: scaleSize(50),
    paddingHorizontal: scaleSize(5),
    borderRadius: scaleSize(5),
    marginLeft: scaleSize(15),
    marginRight: scaleSize(25),
  },
  buttonInner: {
    flex: 1,
    borderRadius: scaleSize(5),
    justifyContent: "center",
    alignItems: "center",
    textAlign: "center",
  },
});

function Button({ title, onPress }: any) {
  return (
    <TouchableOpacity
      style={{
        paddingHorizontal: scaleSize(12),
        paddingVertical: scaleSize(10),
      }}
      onPress={onPress}
    >
      <Text
        style={{ color: "#fff", fontWeight: "bold", fontSize: scaleSize(14) }}
      >
        {title}
      </Text>
    </TouchableOpacity>
  );
}
